import React from "react"
import { MyTableCellHelpers, HUtils } from "@macashipo/mlib"
export default function MTCSuperMan(props) {
  const { extra, row, cell, index } = props
  let _type = HUtils.get(extra, "Type.type")
  let _fieldName = HUtils.get(extra, "FieldName")
  if (row[`UI_CellType_${_fieldName}`]) {
    _type = row[`UI_CellType_${_fieldName}`]
  }
  // console.warn("props", props)
  if (MyTableCellHelpers.Types[_type] && _type != "superman") {
    let TagCell = MyTableCellHelpers.Types[_type]
    return <TagCell cell={cell} row={row} extra={extra} index={index} />
  }
  return <div className="mct-superman-empty"></div>
}
